<template>
  <div>
    <b-form-group>
      <b-input-group>
        <b-form-input type="search" placeholder="Search..." v-model="searchText" autofocus debounce="300" />
        <b-input-group-append>
          <b-button>
            <b-icon icon="search"></b-icon>
          </b-button>
        </b-input-group-append>
      </b-input-group>
    </b-form-group>
    <b-table striped hover small :fields="fields" :items="users" :sort-by.sync="sortBy" :sort-desc.sync="sortDesc">
      <template v-slot:cell(email)="{ item, value }">
        <router-link :to="{ name: `users`, params: { id: item.userId, userTypeId: userTypeId }}">{{ value }}</router-link>
      </template>
      <template v-slot:cell(dateVerified)="{ item, value }">
        <div v-if="!defaultDate(value)">{{ localDate(value) }}</div>
        <router-link v-else :to="{ name: 'authentication', params: { authenticationView: 'verify-sign-up' }, query: { code: item.verificationCode } }" class="text-muted">Verify Login</router-link>
      </template>
    </b-table>
  </div>
</template>

<script>
export default {
  props: {
    userTypeId: String,
  },
  computed: {
    users() {
      let fields = this.fields.map((x) => x.key);
      return this.items.filter((x) => {
        for (let prop in fields) {
          let field = fields[prop];
          let exists = `${x[field]}`.toLowerCase()
            .includes(this.searchText.toLowerCase());
          if (exists) return exists;
        }
        return false;
      });
    },
  },
  async mounted() {
    await this.load();
  },
  methods: {
    async load() {
      try {
        let query = { userTypeId: this.userTypeId };
        let url = `BackendServices/Users`;
        let response = await this.$http.get(url, { query });
        this.items = response.result;
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
    defaultDate(utc) {
      return utc == "0001-01-01T00:00:00";
    },
    localDate(utc) {
      utc = utc.replace("T", " ");
      if (utc == "0001-01-01 00:00:00") return "";
      let date = new Date(`${utc} UTC`);
      return date.toLocaleString();
    },
  },
  data() {
    return {
      items: [],
      fields: [
        { key: "firstName", sortable: true },
        { key: "lastName", sortable: true },
        { key: "email", sortable: true },
        { key: "userRoleName", sortable: true, label: "Role" },
        { key: "accessLevelName", sortable: true, label: "Permission" },
        { key: "dateVerified", sortable: true, label: "Verified" },
      ],
      sortBy: "name",
      sortDesc: false,
      searchText: "",
    };
  },
};
</script>
